import React, { Component } from 'react';
import { Link} from 'gatsby';


import Welcome from './Welcome';


function scrollToBottom(duration) {
    if (duration <= 0) return;
    var difference = document.documentElement.scrollHeight - window.scrollY;
    var perTick = difference / duration * 10;

    setTimeout(function() {
        window.scroll(0, window.scrollY + perTick);
        scrollToBottom(duration - 10);
    }, 10);
}

var SELECTED_STYLE = {color: 'white', fontWeight: '600'}

class MenuBar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            'percent': 0,
            'bgClass': '',
        }
    }

    handleScroll(percent) {
        this.setState({percent})
    }

    render() {
        return (
            <div>
                <div className={"menu__black-bg " + this.state.bgClass} style={{opacity : this.state.percent-0.5}}/>
                <div>
                    <div style={{position: 'fixed', top: 0, width: '100vw', mixBlendMode: 'difference', zIndex: 10}}>
                        <div className="menu__flex-container">
                            <div className="menu__title" style={{opacity : 3*this.state.percent-0.5}}>
                                David Cheng
                            </div>

                            <div className="menu__button-group">
                                <Link to='/'>
                                    <button style={(this.props.selected===0) ? SELECTED_STYLE: {}}> About </button>
                                </Link>
                                <Link to='/resume' style={{textDecoration:"none"}}> <button style={(this.props.selected===1) ? SELECTED_STYLE: {}}>  Resume  </button></Link>
                                <Link to='/'><button style={(this.props.selected===2) ? SELECTED_STYLE: {}} onClick={() => {scrollToBottom(400)}}> Contact </button></Link>
                            </div>
                        </div>
                    </div>
                    
                    {(this.props.welcomeOff) ? 
                    <div />:
                    <Welcome 
                        path='/'
                        blur={this.props.selected===1}
                        onScrollChange={this.handleScroll.bind(this)} 
                        menuBgHidden={() => {this.setState({'bgClass': 'hidden'})}} 
                        menuBgBlack={() => {this.setState({'bgClass': ''})}} 
                        menuBgWhite={() => {this.setState({'bgClass': 'white'})}} 
                    />
                    }

                    
                </div>
            </div>
            
        )
    }
}

export default MenuBar;