// export default Welcome;



const query = graphql`
  query {
    factcheck: file(relativePath: { eq: "factcheck.png" }) {
        ...squareImage
    }

    foresite: file(relativePath: { eq: "foresite.png" }) {
        ...squareImage
    }

    ILLaptop: file(relativePath: { eq: "laptop_IL.png" }) {
        ...squareImage
    }

    Dash1: file(relativePath: { eq: "dash-1.png" }) {
        ...squareImage
    }

    Dash2: file(relativePath: { eq: "dash-2.png" }) {
        ...squareImage
    }

    Dash3: file(relativePath: { eq: "dash-3.png" }) {
        ...squareImage
    }

    Solarhouse1: file(relativePath: { eq: "solarhouse1.jpg" }) {
        ...squareImage
    }

    Solarhouse2: file(relativePath: { eq: "solarhouse2.jpg" }) {
        ...squareImage
    }
  }
`

export const squareImage = graphql`
  fragment squareImage on File {
    childImageSharp {
      fluid(maxWidth: 1000) {
        ...GatsbyImageSharpFluid_withWebp
      }
    }
  }
`

export default (props) => (
    <StaticQuery
        query={query}
        render={(data) => (
            <Welcome {...props} images={data}  />
        )}
    />
)